export default class YoutubeIframe extends React.Component {
    constructor(p) {
        super(p)
        this.player
        this.playButton
        this.state = {
            posterVisible: true
        }
    }

    setPosterVisible = (posterVisible) => {
        this.setState({...this.state, posterVisible})
    }

    play = () => {
        if( this.player ) {
            this.setPosterVisible(false)
            this.player.playVideo()
        }
    }

    componentDidMount() {
        let {iframeId, videoId } = this.props
        let {player, playButton, setPosterVisible} = this
        if( window.youTubeIframeAPIReady )
            createPlayer()
        else {
            if( !window.iframeAPIScriptAppended ) {

                window.iframeAPIScriptAppended = true
                const script = document.createElement('script')
                document.body.appendChild(script)
                script.src = "https://www.youtube.com/iframe_api"
                window.onYouTubeIframeAPIReady = () => {
                    window.youTubeIframeAPIReady = true
                }
            }

            let createPlayerTimeout = setInterval( () => {
                if( window.youTubeIframeAPIReady ) {
                    createPlayer()
                    clearInterval(createPlayerTimeout)
                }
            },100)


        }

        function createPlayer() {
            player = new YT.Player(iframeId, {
                events: {
                  'onReady': onPlayerReady
                }
            })
        }

        function onPlayerReady(event) {
            playButton.addEventListener("click", function() {
                setPosterVisible(false)
                player.playVideo();
            })
        }
    }

    componentWillUnmount() {
        if( this.player )
            this.player.destroy()
    }

    render() {
        let {posterVisible} = this.state
        let {poster, posterClassname, width, height, iframeId, containerClassname, youtubeSrc} = this.props
        return <div className={containerClassname}>
            <div className={`${posterClassname} ${posterVisible && 'visible'}`}>
                <img ref={ e => {this.playButton = e} } src={poster} />
                <div className="playbtn">
                    <span className='en'>PLAY</span>
                </div>
            </div>
            <iframe id={iframeId} width={width} height={width} src={`https://www.youtube.com/embed/${youtubeSrc}?enablejsapi=1&modestbranding=1&autohide=1&showinfo=0`} frameBorder="0" allow="autoplay" allowFullScreen></iframe>
        </div>
    }
}


