import App from 'App'
import store from 'Store'
import { HashRouter } from 'react-router-dom'


let AppRouter = <HashRouter><App/></HashRouter>


window.onload = () => {
    ReactDOM.render(
        <Provider store={store}>
            {AppRouter}
        </Provider>,
        document.getElementById('root')
    );
};












