const MobileItem = ({item}) => {
	let {name, location, id} = item
	return <div className="item-team">
            <a href={`#/works/${id}`}>
    			<div className="teamcover">
    				<img src={`${process.env.PUBLIC_PATH}/asset/${location}/main.jpg`}/>
    			</div>
            </a>
			<div className="teamname">
				<h1>{name}</h1>
			</div>
		</div>
}
	


export default MobileItem


