import "WorkList.css"
import Arrow from 'Arrow'
import MobileItem from 'MobileItem'
import workData from 'workData'
import isMobile from 'isMobile'

export default class WorkList extends React.Component {
    constructor(p){
        super()
        let { teamcover } = this
        this.state={
            nowCover: null,
        }
    }
    changeBackground = (e) => {
        let { teamcover } = this
        let body = document.body
        body.style.backgroundColor ='#fff'
        teamcover.style.backgroundImage = `url(${process.env.PUBLIC_PATH}asset/${e}/circle.jpg)`
        teamcover.style.opacity = '1'
    }
    disappearBackground = (e) => {
        let { teamcover } = this
        let body = document.body
        body.style.backgroundColor ='#222'
        teamcover.style.opacity = '0'
    }
    componentDidMount(){
        let { teamcover } = this
        let { match:params } = this.props

        window.scrollTo(0,0)
        if(window.innerWidth >= 1024 && params.path == '/works'){
            document.body.style.backgroundColor ='#222'
            document.body.style.transition ='.5s'
            teamcover.style.opacity = '0'      
        }else{
            document.body.style.backgroundColor ='#fff'
        }
        
    }
    render() {
        return <section className='work-list page'>
        	<div className="mobile-list">
        		{ workData == null ? null : 
                    workData.map((e, id) => <MobileItem key={id} item={e}/>)
                }
        	</div>
        	<div className="desktop-list">
        		<div className="teams">
                    <div className="item-team">
                        <a href='#/works/1'>
            				<div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('8sip18second')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
            					<img src={`${process.env.PUBLIC_PATH}/asset/8sip18second/eyeball.png`}/>
            				</div>
            				<span className="teamname">8等分18秒</span>
            			</a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/2'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('anorangewish')}}
                                 onMouseLeave = {this.disappearBackground}
                                >
                                <img src={`${process.env.PUBLIC_PATH}/asset/anorangewish/eyeball.png`}/>
                            </div>
                            <span className="teamname">柑願</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/3'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('banjon')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/banjon/eyeball.png`}/>
                            </div>
                            <span className="teamname">包中</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/4'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('bendingdown')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/bendingdown/eyeball.png`}/>
                            </div>
                            <span className="teamname">彎下腰</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/5'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('diamondsutra')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/diamondsutra/eyeball.png`}/>
                            </div>
                            <span className="teamname en">Diamondsutra</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/6'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('exquisite')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/exquisite/eyeball.png`}/>
                            </div>
                            <span className="teamname">金漬</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/7'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('fragrantunderstanding')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/fragrantunderstanding/eyeball.png`}/>
                            </div>
                            <span className="teamname">香識</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/8'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('frankenstein')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/frankenstein/eyeball.png`}/>
                            </div>
                            <span className="teamname">弗婪肯斯坦</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/9'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('guanlaisianne')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/guanlaisianne/eyeball.png`}/>
                            </div>
                            <span className="teamname">諺來如此</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/10'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('hoarding')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/hoarding/eyeball.png`}/>
                            </div>
                            <span className="teamname">囤</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/11'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('innerlights')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/innerlights/eyeball.png`}/>
                            </div>
                            <span className="teamname">藏芒</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/12'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('pickupstone')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/pickupstone/eyeball.png`}/>
                            </div>
                            <span className="teamname">拾石</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/13'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('redbrickstroy')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/redbrickstroy/eyeball.png`}/>
                            </div>
                            <span className="teamname">磚語</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/14'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('ridiculousexcousenews')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/ridiculousexcousenews/eyeball.png`}/>
                            </div>
                            <span className="teamname">藉口報報</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/15'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('ssuhsiutsai')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/ssuhsiutsai/eyeball.png`}/>
                            </div>
                            <span className="teamname">四季仔</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/16'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('thesunnyshow')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/thesunnyshow/eyeball.png`}/>
                            </div>
                            <span className="teamname en">The Sunny Show</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/17'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('truth')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/truth/eyeball.png`}/>
                            </div>
                            <span className="teamname">都給你說就好啦</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/18'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('what')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/what/eyeball.png`}/>
                            </div>
                            <span className="teamname">蝦米</span>
                        </a>
                    </div>
                    <div className="item-team">
                        <a href='#/works/19'>
                            <div className="eyeball"
                                 onMouseOver={(e) => {this.changeBackground('yesido')}}
                                 onMouseLeave = {this.disappearBackground}
                                 >
                                <img src={`${process.env.PUBLIC_PATH}/asset/yesido/eyeball.png`}/>
                            </div>
                            <span className="teamname en">Yes,I do</span>
                        </a>
                    </div>
        		</div>
                <div className="teamcover"
                     ref = { el => this.teamcover = el }>
                </div>
        		
        	</div>
        </section>
    }
}


