import "ContactPage.css"
import Arrow from 'Arrow'


export default class ContactPage extends React.Component {
    componentDidMount(){
        window.scrollTo(0,0)
        let body = document.body
        body.style.backgroundColor ='#fff'
        body.style.transition ='0s'
    }
    render() {
        return <section className='page contact-page'>
        	<div className="text-content">
        		<div className="email">
	        		<p>若您需要詢問/建議我們，</p>
					<p>歡迎聯絡<b><a href='mailto:mcvcd108@gmail.com'>mcvcd108@gmail.com</a></b></p>
					
	        	</div>
	        	<div className="facebook">
	        		<p>
	        			或是追蹤我們的<b><a href='https://www.facebook.com/mcutvcd108/' target='_blank'>FACEBOOK</a></b>
	        		</p>
	        	</div>
        	</div>
        	<div className="deco">
        		<img src={`${process.env.PUBLIC_PATH}asset/contact-dot.png`}/>
        	</div>
        	
        </section>
    }
}


